import React, { Component } from 'react'
import { Link as ScrollLink } from 'react-scroll'

class EmployersPageSectionNavTabs extends Component {
  render () {
    const { activeTab } = this.props
    return (
      <div className='employers-page__section-nav-container'>
        <ul>
          <li className={`employers-page__section-nav-link`}>
            <ScrollLink offset={-100} smooth to='screened-community'>
              <span style={activeTab === 'community' ? {color: '#4353FF'} : {}}>Community</span>
            </ScrollLink>
          </li>
          <li className='employers-page__section-nav-link'>
            <ScrollLink offset={-100} smooth to='talent-management'>
              <span style={activeTab === 'platform' ? {color: '#22DDAB'} : {}}>Platform</span>
            </ScrollLink>
          </li>
          <li className='employers-page__section-nav-link'>
            <ScrollLink offset={-100} smooth to='unparalleled-support'>
              <span style={activeTab === 'support' ? {color: '#EDB726'} : {}}>Support</span>
            </ScrollLink>
          </li>
        </ul>
      </div>
    )
  }
}

export default EmployersPageSectionNavTabs



