import EmployersPageWeb from './EmployersPage'
import EmployersPageMobile from './EmployersPage.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const EmployersPage = (props) => props.isMobile ? <EmployersPageMobile {...props} /> : <EmployersPageWeb {...props} />

export default withDeviceWidthDetector(EmployersPage)



