import React, { Component } from 'react'
import { PrimaryButton, PrimaryButtonOutline } from 'Shared/components/Button'
import { StaticRoutes } from 'Shared/constants/Routes'
import { t } from 'i18next'
import './EmployerLPMainCTAButtons.scss'
import withTranslations from 'Shared/hoc/withTranslations'
import { withRouter } from 'react-router-dom'
import cx from 'classnames'

class EmployerLpMainCtaButtons extends Component {
  redirectToPricing = () => this.props.history.push(StaticRoutes.pricing)
  redirectToSignUp = () => this.props.history.push(StaticRoutes.employer.signUp)

  render () {
    const { isCentered } = this.props

    const buttonClasses = cx({
      'employers-container__main-cta-buttons': true,
      'employers-container__main-cta-buttons-centered': isCentered
    })

    return (
      <div className={buttonClasses}>
        <button type='button' onClick={this.redirectToPricing} className='employers-container__intro-heading--post-job-button'>
          {t('employersPage.ctaButtons.postJob')}
        </button>
        {/* <button type='button' onClick={this.redirectToSignUp} className='employers-container__intro-heading--demo-account-button'>
          {t('employersPage.ctaButtons.createDemoAccount')}
        </button> */}
      </div>
    )
  }
}

export default withRouter(withTranslations(EmployerLpMainCtaButtons))



