import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import EmployersPage from 'Shared/layouts/EmployersPage'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { getEmployerLandingPage, getCompanyLogos } from 'Shared/selectors/geolocation'

const mapStateToProps = (state, ownProps) => ({
  employerLandingPage: getEmployerLandingPage(state),
  companyLogos: getCompanyLogos(state),
})

const mapDispatchToProps = dispatch => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EmployersPage))



