import React, { Component, createRef  } from 'react'
import { Helmet } from 'react-helmet'
import withTranslations from 'Shared/hoc/withTranslations'
import ModalPlayer from 'Shared/components/ModalPlayer'
import { withRouter } from 'react-router-dom'
import * as R from 'ramda'

import CompanyLogos from 'Shared/components/CompanyLogos'
import LazyLoad, { forceCheck as forceCheckForLazyLoadInView } from 'react-lazyload'

import { ExternalRoutes } from 'Shared/constants/Routes'

import ShortlistingSupportImage from 'Shared/assets/images/vectors/emp-support-1.svg'
import ResourcesSupportImage from 'Shared/assets/images/vectors/emp-support-2.svg'

import AttractTalentImage from 'Shared/assets/images/brand/attract-talent-image.png'
import Jpeg2000AttractTalentImage from 'Shared/assets/jpeg2000/attract-talent-image.jpf'
import WebpAttractTalentImage from 'Shared/assets/webp/attract-talent-image.webp'
import JpegxrAttractTalentImage from 'Shared/assets/jpegxr/attract-talent-image.wdp'

import GetMatchedImage from 'Shared/assets/images/brand/get-matched-image.png'
import WebpGetMatchedImage from 'Shared/assets/webp/get-matched-image.webp'
import Jpeg2000GetMatchedImage from 'Shared/assets/jpeg2000/get-matched-image.jpf'
import JpegxrGetMatchedImage from 'Shared/assets/jpegxr/get-matched-image.wdp'

import ManageApplicationsImage from 'Shared/assets/images/brand/manage-applications-image.png'
import WebpManageApplicationsImage from 'Shared/assets/webp/manage-applications-image.webp'
import Jpeg2000ManageApplicationsImage from 'Shared/assets/jpeg2000/manage-applications-image.jpf'
import JpegxrManageApplicationsImage from 'Shared/assets/jpegxr/manage-applications-image.wdp'

import MessageImage from 'Shared/assets/images/brand/message-image.png'
import WebpMessageImage from 'Shared/assets/webp/message-image.webp'
import Jpeg2000MessageImage from 'Shared/assets/jpeg2000/message-image.jpf'
import JpegxrMessageImage from 'Shared/assets/jpegxr/message-image.wdp'

import IconConnect from 'Shared/assets/images/vectors/emp-feature-2.svg'
import IconSupport from 'Shared/assets/images/vectors/emp-feature-4.svg'
import IconTeam from 'Shared/assets/images/vectors/emp-feature-1.svg'
import TestimonialImage from 'Shared/assets/images/other/thorsten-testimonial.webp'
import ThyssenkruppLogo from 'Shared/assets/images/logos/Thyssenkrupp_Logo.svg'

import withScripts from 'TrackingScripts'

import { employersPage } from 'Shared/constants/MetaTags'
import offsets from 'Shared/constants/LazyLoad'
import Slider from 'react-slick'
import { Link as ScrollLink, Element } from 'react-scroll'

import './EmployersPage.scss'
import EmployersPageSectionNavTabs from 'Shared/layouts/EmployersPage/EmployersPageSectionNavTabs'
import { PrimaryButtonOutline } from 'Shared/components/Button'
import CommunityImage from 'Shared/assets/images/illustrations/employerlp-community.svg'
import EmployerLpMainCtaButtons from 'Employer/components/EmployerLPMainCTAButtons/EmployerLPMainCTAButtons'
import EmployerLPMainCTAButtonsHire from '../../../Employer/components/EmployerLPMainCTAButtons/EmployerLPMainCTAButtonsHire'

class EmployersPageMobile extends Component {
  state = {
    showVideo: false,
    buttonToggled: false,
  }

  constructor(props) {
    super(props);
    this.testimonialImageRef = createRef();
  }

  handleIconClick = () => {
    this.testimonialImageRef.current.click();
  };

  handleToggle = () => {
    const { buttonToggled } = this.state;
    this.setState({ buttonToggled: !buttonToggled });

    const url = buttonToggled ? '/employers' : '/';
    this.props.history.push(url);
  };

  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
    setTimeout(forceCheckForLazyLoadInView, 1000)
  }

  platformFeatures = [
    {
      name: 'attractTalent',
      title: this.props.t('employersPage.platformSection.applicantsFeatures.attractTalent.title'),
      description: this.props.t('employersPage.platformSection.applicantsFeatures.attractTalent.description'),
      image: {
        default: AttractTalentImage,
        webp: WebpAttractTalentImage,
        jpeg2000: Jpeg2000AttractTalentImage,
        jpegxr: JpegxrAttractTalentImage
      }
    },
    {
      name: 'getMatched',
      title: this.props.t('employersPage.platformSection.applicantsFeatures.getMatched.title'),
      description: this.props.t('employersPage.platformSection.applicantsFeatures.getMatched.description'),
      image: {
        default: GetMatchedImage,
        webp: WebpGetMatchedImage,
        jpeg2000: Jpeg2000GetMatchedImage,
        jpegxr: JpegxrGetMatchedImage
      }
    },
    {
      name: 'manageApplications',
      title: this.props.t('employersPage.platformSection.applicantsFeatures.manageApplications.title'),
      description: this.props.t('employersPage.platformSection.applicantsFeatures.manageApplications.description'),
      image: {
        default: ManageApplicationsImage,
        webp: WebpManageApplicationsImage,
        jpeg2000: Jpeg2000ManageApplicationsImage,
        jpegxr: JpegxrManageApplicationsImage
      }
    },
    {
      name: 'message',
      title: this.props.t('employersPage.platformSection.applicantsFeatures.message.title'),
      description: this.props.t('employersPage.platformSection.applicantsFeatures.message.description'),
      image: {
        default: MessageImage,
        webp: WebpMessageImage,
        jpeg2000: Jpeg2000MessageImage,
        jpegxr: JpegxrMessageImage
      }
    }
  ]
  // eslint-disable-next-line
  forceResize = () => setTimeout(() => window.dispatchEvent(new Event('resize')), 0)

  showVideo = () => this.setState({ showVideo: !this.state.showVideo })

  render () {
    const { t, employerLandingPage, companyLogos } = this.props
    const { buttonToggled } = this.state;

    return (
      <section>
        <Helmet>
          <title>{employerLandingPage.metaTitle}</title>
          <meta name='description' content={employerLandingPage.metaDescription} />
          <meta property='og:title' content={employerLandingPage.metaTitle} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={employersPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={employersPage.url} />
          <meta property='og:description' content={employerLandingPage.metaDescription} />
        </Helmet>
        <section className="employers-page-mobile__section">
        <div className='employers-container parsing--hidden'>
        <section className="employers-page-mobile__heading-section">
        <div className='container'>
        <div className='employer-section-mobile__switch-slider-div'>
        <svg className='svg_arrow fadeRightArrow' width="55" height="18" viewBox="0 0 55 18" fill="none" xmlns="http://www.w3.org/2000/svg">
        <g clip-path="url(#clip0_217_986)">
        <path d="M52.4327 12.3352C40.0724 8.32555 26.8831 7.44421 14.1586 10.0865C10.4402 10.8586 6.78508 11.9126 3.20635 13.2035C2.14082 13.5879 0.918762 12.7667 0.63427 11.7026C0.31728 10.5169 1.02523 9.4448 2.09465 9.05903C15.0033 4.40254 28.8562 3.12513 42.3555 5.41991C46.146 6.06427 49.8812 7.00242 53.5444 8.19078C56.1017 9.02038 55.0094 13.1711 52.4327 12.3352Z" fill="white"/>
        <path d="M8.18502 3.23311L6.02234 7.41438L4.9101 9.56475L4.35398 10.6399C4.28165 10.7798 4.21253 11.0038 4.10682 11.1178C4.53715 10.6536 2.91466 9.26656 3.67766 9.54866C4.35127 9.79772 5.00585 10.2727 5.63858 10.6176C6.33066 10.9949 7.02275 11.3722 7.71483 11.7495C9.099 12.504 10.4832 13.2586 11.8673 14.0132C12.835 14.5407 13.2119 16.0057 12.6175 16.9534C11.9831 17.9647 10.7917 18.2886 9.7567 17.7243C8.14183 16.844 6.52697 15.9637 4.9121 15.0834C4.14312 14.6642 3.37413 14.245 2.60515 13.8258C1.64513 13.3024 0.644581 12.87 0.195159 11.7754C-0.264039 10.6571 0.164463 9.58936 0.681313 8.59011L1.97893 6.08135C2.844 4.40883 3.70908 2.73633 4.57415 1.06382C5.08829 0.0697932 6.51222 -0.318539 7.43492 0.292859C8.41819 0.944401 8.73497 2.16985 8.18502 3.23311Z" fill="white"/>
        </g>
        <defs>
        <clipPath id="clip0_217_986">
        <rect width="55" height="18" fill="white" transform="matrix(-1 0 0 1 55 0)"/>
        </clipPath>
        </defs>
        </svg>

          <label className='employer-section__candidate-label'>{t('candidatesPage.switchButtons.firstOption')}</label>
            <label className='employer-section__switch'>
              <input
                type='checkbox'
                className='employer-section__switch-input'
                checked={buttonToggled}
                onChange={this.handleToggle}
              />
              <span className='employer-section__switch-slider' />
            </label>
          <label className='employer-section__employer-label'>{t('candidatesPage.switchButtons.secondOption')}</label>
        </div>
            <div className='employers-page-mobile__heading'>
              <h1 className='employers-page-mobile__heading-title fadeDown'>{employerLandingPage.headline}</h1>
              <p className='employers-page-mobile__heading-description fadeDown'>
                {employerLandingPage.description}
              </p>
              <div className='employers-page-mobile__heading-contact fadeDown'>
                <EmployerLpMainCtaButtons />
              </div>
            </div>
          </div>
          </section>
          <section className="employers-page-mobile__testimonials-section">
          <div className='container'>
            <div className='employers-page-mobile__testimonials'>
              <div>
                <div className='employers-page-mobile__testimonials-heading'>
                  {t('employersPage.moreClients')}
                </div>
                {
                  R.not(R.isNil(companyLogos)) &&
                  <div className='employers-page-mobile__testimonials-logos'>
                    <CompanyLogos companyLogos={companyLogos} />
                  </div>
                }
              </div>
              {/* <div>
                <div className='employers-page-mobile__testimonials-item'>
                  <div className='employers-page-mobile__testimonial-video'>
                    <ModalPlayer
                      videoUrl='https://vimeo.com/300133760/919c6416c0'
                      customTrigger={<img src={TestimonialImage} ref={this.testimonialImageRef} />}
                    />
                  </div>
                  <div className='employers-page-mobile__testimonial-description'>
                    <p className='employers-page-mobile__testimonial-quote'>
                      "{t('employersPage.testimonial.text')}"
                    </p>
                    <div className='employers-page__bottom_bar_name'>
                      <img src={ThyssenkruppLogo} alt='Thyssenkrupp' />
                        <div className="employers-page__testimonial-author-wrapper">
                          <p className='employers-page__testimonial-author-name'>Thorsten Sickelmann</p>
                          <p className='employers-page__testimonial-author-position'>{t('employersPage.testimonial.jobTitle')}</p>
                        </div>
                          <i className='icon-im-circle-play employers-page__testimonials-icon' onClick={this.handleIconClick}/>       
                    </div>
                  </div>
                </div>
                <div className='employers-page-mobile__testimonials-heading-cta'>
                    <a href={ExternalRoutes.customers} target='_blank'>
                    <PrimaryButtonOutline
                      type='button'
                    >
                      Customer Stories
                    </PrimaryButtonOutline>
                    </a>
                  </div>
              </div> */}
            </div>
          </div>
          </section>
          <section className="employers-page-mobile__benefits-section">
          <div className='container'>
            <div className='employers-page-mobile__benefits'>
              <div className='employers-page-mobile__benefits-heading'>
                {t('employersPage.aboutOliv.moreAbout')}
              </div>
              <ScrollLink offset={-100} smooth to='screened-community'>
                <div className='employers-page-mobile__benefits-item'>
                  <div className='employers-page-mobile__benefits-item-icon'>
                    <LazyLoad offset={offsets.small}>
                      <img src={IconTeam} />
                    </LazyLoad>
                  </div>
                  <div className='employers-page-mobile__benefits-item-heading'>
                    {t('employersPage.aboutOliv.communityHeading')}
                  </div>
                  <div className='employers-page-mobile__benefits-item-description'>
                    {t('employersPage.aboutOliv.communityHeadingMore')}
                  </div>
                  <div className='employers-page-mobile__benefits-item-more'>
                    {t('employersPage.learnMore')} <i className='icon-im-caret-right' />
                  </div>
                </div>
              </ScrollLink>

              <ScrollLink offset={-100} smooth to='talent-management'>
                <div className='employers-page-mobile__benefits-item'>
                  <div className='employers-page-mobile__benefits-item-icon'>
                    <LazyLoad offset={offsets.small}>
                      <img src={IconConnect} />
                    </LazyLoad>
                  </div>
                  <div className='employers-page-mobile__benefits-item-heading'>
                    {t('employersPage.aboutOliv.talentHeading')}
                  </div>
                  <div className='employers-page-mobile__benefits-item-description'>
                    {t('employersPage.aboutOliv.talentHeadingMore')}
                  </div>
                  <div className='employers-page-mobile__benefits-item-more'>
                    {t('employersPage.learnMore')} <i className='icon-im-caret-right' />
                  </div>
                </div>
              </ScrollLink>

              <ScrollLink offset={-100} smooth to='unparalleled-support'>
                <div className='employers-page-mobile__benefits-item'>
                  <div className='employers-page-mobile__benefits-item-icon'>
                    <LazyLoad offset={offsets.small}>
                      <img src={IconSupport} />
                    </LazyLoad>
                  </div>
                  <div className='employers-page-mobile__benefits-item-heading'>
                    {t('employersPage.aboutOliv.supportHeading')}
                  </div>
                  <div className='employers-page-mobile__benefits-item-description'>
                    {t('employersPage.aboutOliv.supportHeadingMore')}
                  </div>
                  <div className='employers-page-mobile__benefits-item-more'>
                    {t('employersPage.learnMore')} <i className='icon-im-caret-right' />
                  </div>
                </div>
              </ScrollLink>

            </div>
          </div>
          </section>
          <section className="employers-page-mobile__community-section">
          <Element name='screened-community' />
          <div className='container'>
            <div className='employers-page-mobile__community'>
              <EmployersPageSectionNavTabs activeTab='community' />
              <div className='employers-page-mobile__community-heading-title'>
                {t('employersPage.platformSection.communityHeading')}
              </div>
              <div>
                <LazyLoad offset={offsets.small}>
                  <img className='employers-page-mobile__community-image' src={CommunityImage} alt='Oliv Support' />
                </LazyLoad>
              </div>
              <div className='employers-page-mobile__community-description'>
                {t('employersPage.platformSection.communityHeadingMore')}
              </div>
              {/* <div className='employers-page-mobile__community-cta'>
                <a href={ExternalRoutes.screening} target='_blank'>
                  {t('employersPage.platformSection.learnMore')} <i className='icon-im-caret-right' />
                </a>
              </div> */}
            </div>
          </div>
          <Element name='talent-management' />
          </section>
          <section className="employers-page-mobile__platform-section">
          <div className='container'>
            <div className='employers-page-mobile__platform'>
              <EmployersPageSectionNavTabs activeTab='platform' />
              <div className='employers-page-mobile__platform-heading-title'>
                {t('employersPage.platformSection.applicantsHeading')}
              </div>
              <div className='employers-page-mobile__platform-carousel'>
                <Slider
                  dots
                  infinite
                  adaptiveHeight
                  speed={500}
                  slidesToShow={1}
                  slidesToScroll={1}
                  arrows={false}
                >
                  {this.platformFeatures.map((feature, index) =>
                    <div className='employers-page-mobile__platform-carousel-item' key={feature.name + index}>
                      <picture>
                        <source srcSet={feature.image.webp} type='image/webp' />
                        <source srcSet={feature.image.jpeg2000} type='image/jp2' />
                        <source srcSet={feature.image.jpegxr} type='image/wdp' />
                        <img src={feature.image.default} alt={feature.title} onLoad={this.forceResize} />
                      </picture>
                      <div className='employers-page-mobile__platform-description-list-item'>
                        <div className='employers-page-mobile__platform-description-list-item-title'>
                          {feature.title}
                        </div>
                        <div className='employers-page-mobile__platform-description-list-item-description'>
                          {feature.description}
                        </div>
                      </div>
                    </div>
                  )}
                </Slider>
              </div>
            </div>
          </div>
          </section>
          <section className="employers-page-mobile__support-section">
          <Element name='unparalleled-support' />
          <div className='container'>
            <div className='employers-page-mobile__support'>
              <EmployersPageSectionNavTabs activeTab='support' />
              <div className='employers-page-mobile__support-heading-title'>
                {t('employersPage.supportSection.heading')}
              </div>
              <div className='employers-page-mobile__support-heading-description'>
                {t('employersPage.supportSection.headingMore')}
              </div>
              <div className='employers-page-mobile__support-items'>
                <LazyLoad offset={offsets.small}>
                  <div className='employers-page-mobile__support-item'>
                    <div className='employers-page-mobile__support-item-image'>
                      <img src={ResourcesSupportImage} />
                    </div>
                    <div className='employers-page-mobile__support-item-description-title'>
                      {t('employersPage.supportSection.onBoardingAndResources.title')}
                    </div>
                    <div className='employers-page-mobile__support-item-description-text'>
                      {t('employersPage.supportSection.onBoardingAndResources.description')}
                    </div>
                  </div>
                </LazyLoad>
                <LazyLoad offset={offsets.small}>
                  <div className='employers-page-mobile__support-item'>
                    <div className='employers-page-mobile__support-item-image'>
                      <img src={ShortlistingSupportImage} />
                    </div>
                    <div className='employers-page-mobile__support-item-description-title'>
                      {t('employersPage.supportSection.dedicatedHiringSupport.title')}
                    </div>
                    <div className='employers-page-mobile__support-item-description-text'>
                      {t('employersPage.supportSection.dedicatedHiringSupport.description')}
                    </div>
                  </div>
                </LazyLoad>
              </div>
            </div>
          </div>
          </section>
          <section className="employers-page-mobile__create-account-section">
          <div className='container'>
            <div className='employers-page-mobile__create-account'>
              <div className='employers-page-mobile__create-account-heading'>
                {t('employersPage.ctaSection.heading')}
              </div>
              <div className='employers-page-mobile__create-account-description'>
                {t('employersPage.ctaSection.description')}
              </div>
              <EmployerLPMainCTAButtonsHire isCentered />
            </div>
          </div>
          </section>
        </div>
        </section>
      </section>
    )
  }
}

EmployersPageMobile.defaultProps = {
  employerLandingPage: {
    metaTitle: '',
    metaDescription: '',
    headline: '',
    description: '',
    heroImage: ''
  }
}

export default withRouter(withScripts(withTranslations(EmployersPageMobile)))



